import React from "react"
import {graphql, useStaticQuery} from "gatsby"

import Layout from "../components/Layout"
import Analysers from "../components/Analysers"
import BlockOfText from "../components/BlockOfText"
import Sponsors from "../components/Sponsors"
import MadeBy from "../components/MadeBy"

const IndexPage = () => {
  const {
    site: {
      siteMetadata: {title},
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <Layout title="Home">
      <section className="row">
        <p className="column">
          {title} is a set of complementary SEO tools for JAMstack powered
          blogs. Use the tools below to SEO optimize your blog posts content,
          titles and descriptions.
        </p>
      </section>
      <Analysers />
      <section>
        <div className="row">
          <div className="column column-50">
            <BlockOfText title="About">
              <p>
                Hi, my name is Adam. I started JAM SEO tools project out of
                frustration I experienced after I moved my blog from Wordpress
                to JAMstack. While WP offer many SEO plugins that are extremaly
                handy while we write blog posts, I couldnâ€™t find any similar a
                solution for JAMstack. I hope this project will fill that gap.
              </p>
              <p>
                I will add more tools and features over time. If you have a
                feature request drop me a message at Twitter.
              </p>
              <p>
                If you want to sponsor this project drop me a message at
                Twitter.
              </p>
            </BlockOfText>
          </div>
          <div className="column column-50 ">
            <Sponsors />
          </div>
        </div>
      </section>
      <MadeBy />
    </Layout>
  )
}

export default IndexPage
