import React from "react"

import "./style.scss"

const KeywordPicker = ({
  title,
  instructions,
  onChange,
  name,
  keywords = "",
  className = "",
}) => {
  return (
    <section className={`keyword-picker ${className}`}>
      <h2>{title}</h2>
      <p>{instructions}</p>
      <label>
        <input
          className="keyword-picker"
          name={name}
          type="text"
          value={keywords}
          onChange={e => onChange(e.target.value)}
        />
      </label>
    </section>
  )
}

export default KeywordPicker
